.section__signHistory {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: $clr-background;
  .logo {
    display: none;
    position: absolute;
    top: 56px;
    left: 40px;
    width: 89px;
    @include for-desktop {
      display: block;
      bottom: -10px;
    }
    &__img {
      width: 100%;
      object-fit: cover;
    }
  }
  .signHistory__bg {
    display: none;
    @include for-desktop {
      display: block;
      position: absolute;
      inset: 0;
      margin: auto;
    }
  }
  .signHistory__buildFake {
    position: absolute;
    top: 90%;
    left: 50%;
    translate: -50% -50%;
    padding: 8px;
    & span {
      color: $clr-green;
      font-weight: 700;
    }
    &:hover {
      border: 1px solid $clr-green;
      border-radius: 16px;
    }
    &--del {
      span {
        color: #ca0000;
      }
      &:hover {
        border: 1px solid #ca0000;
      }
    }
  }
}

.signHistory__header {
  width: 100%;
  height: 56px;
  padding: 16px;
  background: $clr-green;
  display: flex;
  justify-content: space-between;
  @include for-desktop {
    justify-content: center;
    .header__homePage {
      display: none;
    }
  }
}

.header__search {
  display: flex;
  align-items: center;
  gap: 5px;
  @include for-desktop {
    display: flex;
  }
  .search__input {
    background: $clr-green;
    border: 1px solid $clr-white;
    @include set-font(1.125rem, 1.625rem, $clr-white);
    display: none;
    &::placeholder {
      color: $clr-white;
    }
    &:focus {
      outline: none;
    }
    @include for-desktop {
      display: block;
      border: 0;
    }
  }

  .search__label {
    img {
      vertical-align: middle;
      cursor: pointer;
    }
  }
}

.signHistory__body {
  padding: 0 16px;
  max-width: 620px;
  margin: 0 auto;
  @include for-desktop {
    margin-top: 30px;
  }
}

.signHistory__list {
  display: flex;
  flex-direction: column;
  gap: 10px;
  padding-bottom: 24px;

  .list__title {
    @include set-font(1rem, 18.75px, $clr-black);
    font-family: 'Roboto';
    font-weight: 700;
    margin-top: 11.5px;
  }

  .list__item {
    display: flex;
    justify-content: space-between;
    padding: 22.5px 25px 22.5px 12px;
    background: $clr-white;
    box-shadow: 1px 4px 6px rgba(0, 0, 0, 0.11);
    border-radius: 13px;

    .item__name {
      margin-left: 12px;
      margin-right: auto;
      background: $clr-gradient;
      color: transparent;
      -webkit-background-clip: text;
      background-clip: text;
    }

    .item__icon {
      vertical-align: middle;
      cursor: not-allowed;
      &:hover {
        scale: 1.2;
      }
    }
  }
}

.signHistory__header.header--noData {
  @include for-desktop {
    display: none;
  }
}
